
.temperature-menu{
  margin-bottom:10px;
  border-radius:5px;
  padding:5px 0px;
  background:#22f;
  color:white;
  width:150px;
  overflow:hidden;
  font-size:2em;
  -webkit-transition:width 0.5s;
  transition: width 0.5s;
}
.temperature-menu.is-open{
  width:300px;
}
.temperature-menu-heading{
  width:300px;    
}
.friendly-name{
  float:left;
  width:50%;
  background: transparent!important;
  text-align: center;
  padding-bottom:0;
}
.temperature-wrap{
  width:300px;
  text-align: center;
}
.current-temp{
  cursor:pointer;
  width:50%;
}
.target-temp{
  width:50%;
}
.button.temp-select{
  margin: 0px;
  padding: 0.5em 1em;
  background: #ddd;
  color:#777;
  opacity: 0.5;
}
.button.temp-select:hover, .button.temp-select:focus{
  background: #ccc!important;
}
.button.temp-select.is-selected{
  background: #a60003!important;
  color:#fff;
  opacity: 1;
}
.button.temp-select.is-selected:hover, .button.temp-select.is-selected:focus{
  background: #900!important;
}

.temperature-menu.is-on {
  background: #c60f13;
}

.label.is-heating {
  -webkit-animation: beat  5s linear infinite;
  animation: beat 5s linear infinite;
}

@keyframes beat {
  0% {
    opacity: 0.7;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.7;
  }
}

@-webkit-keyframes beat {
  0% {
    opacity: 0.7;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.7;
  }
}




